(function($) {
    $(window).resize(function() {
      $('.main .message_line').css({
        'width': $(window).width()});
    });

    var wh = window.innerHeight,
        sections = {
            main: $('.main')[0],
            services: $('.services')[0],
            bio: $('.bio')[0],
            documents: $('documents')[0]
        };

    var ctrl = new ScrollMagic.Controller({
        globalSceneOptions: {
            triggerHook: "onEnter"
        }
    });

    //main
    new ScrollMagic.Scene({
            triggerElement: sections.main
        })
    .setTween(new TimelineLite()
        .from($('.main .message_line'), 0.7, {
            width: 0,
            ease: Power1.easeInOut
        }, 0)
        .from($('.main .message_line .message'), 0.5, {
            opacity: 0,
            top: "10px",
            ease: Power1.easeInOut
        }, 0.6)
        .from($('.main .message_subline'), 0.5, {
            opacity: 0,
            ease: Power1.easeInOut
        }, 1.1))
    //.addIndicators()
    .addTo(ctrl);

})(jQuery);